@mixin title($color) {
  font-family: $font--heading;
  color: $color;
  font-size: 18px;
  letter-spacing: 3.33px;
  // line-height: 33px;
  text-transform: uppercase;
  padding: 0;
  font-weight: 500;

  @include breakpoint(medium) {
    font-size: 20px;
    // line-height: 24px;
  }
}

@mixin subtitle($color) {
  font-family: $font--heading;
  color: $color;
  font-weight: 100;
  font-size: 14px;
  letter-spacing: 0.75px;
  margin: 0;
  padding: 0;

  @include breakpoint(medium) {
    font-size: 16px;
  }
}

