.announcement {
	position:relative;
	z-index:13;
	overflow:hidden;
  height: 18px;
  text-align: center;

	a,
  p {
		display: block;
		width: 100%;
    margin: 0;
		padding: 0;
		text-align: center;
		color: $brand--primary;
		text-transform: uppercase;
    font-size: 20px;
    letter-spacing: 2.25px;
	}

	&__item {
		background: $brand--secondary;
		width: 100%;
    position: absolute;
    transition: 0.6s;
    &.out {
      transform: translateY(-100%);
      transition: 0.6s;
    }
    &.start {
      transform: translateY(100%);
      transition: none;
    }
    &.in {
      transform: translateY(0%);
      transition: 0.6s;
    }
